import React, { FC, memo, ReactNode } from 'react';
import Nav from 'components/Nav';
import Routes from 'components/Routes';
import { RouteInfo } from 'helpers/routes';
import classes from './styles.module.scss';

export const QuestionGroupImpl: FC<QuestionGroupProps> = ({
  title,
  basePath = '',
  routes,
  children,
}) => (
  <div className={classes.Container}>
    <h1 className={classes.Title}>{title}</h1>
    <Nav className="mb-5" basePath={basePath} routes={routes} />
    <Routes basePath={basePath} routes={routes} />
    {children}
  </div>
);

const QuestionGroup = memo(QuestionGroupImpl);
QuestionGroup.displayName = 'QuestionGroup';
export default QuestionGroup;

export interface QuestionGroupProps {
  title: string;
  basePath?: string;
  routes: RouteInfo[];
  children?: ReactNode;
}
